import React,{ Suspense } from 'react'
import {
  CBadge,
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CDataTable,
  CRow,
  CButton
} from '@coreui/react'
import { DocsLink } from 'src/reusable'
import { connect } from "react-redux";
import Swal from 'sweetalert2/dist/sweetalert2.js'
import 'sweetalert2/src/sweetalert2.scss'

import {get_buisnsess_data,delete_buisness} from "../../../redux/allbuisness/action";
import DataTable from 'react-data-table-component';

const fields = ['name','phone','email','action']


  class Allbuisness extends React.Component {
 
   componentDidMount = async () => {

      await this.props.get_buisnsess_data()
      
    }
    confirmDialod = (id) => {
      Swal.fire({
          title: 'Are you sure?',
          text: "Are You Sure you want to delete this User!",
          icon: 'warning',
          showCancelButton: true,
          confirmButtonColor: '#3085d6',
          cancelButtonColor: '#d33',
          confirmButtonText: 'Yes, delete it!'
      }).then((result) => {
          if (result.value) {
              this.props.delete_buisness(id)
          }
      })
  }
     render(){
 const allbuisness = this.props.allbuisness.business_list.businesses;
if(allbuisness ) allbuisness.sort(function(a,b){

  return new Date(b.created_at) - new Date(a.created_at);
});

 return (
    <>
 <CRow>
  <CCol xs="12" lg="12">
          <CCard>
            <CCardHeader>
              Buisnesses
            
            </CCardHeader>
         {allbuisness ?    <CCardBody>
             {allbuisness ?  <CDataTable
              items={allbuisness}
              fields={fields}
              itemsPerPage={10}
              itemsPerPageSelect
              tableFilter
              pagination
              scopedSlots = {{
                'action':
                  (tabledata)=>(
                    <tr>
                      <td><CButton type="submit" size="sm" onClick={e => this.props.history.push('/viewbuisness/' + tabledata.id)}    color="info">view</CButton></td>
                      <td><CButton type="submit" size="sm" onClick={e => this.props.history.push('/editbuisness/' + tabledata.id)}    color="info">edit</CButton></td>
                      <td> <CButton type="submit" size="sm"  color="danger"  onClick={e => this.confirmDialod( tabledata.id)}  >delete</CButton></td>
                    </tr>
                 
                    ) }}

            /> :"" }
           
            </CCardBody> : <div className="d-flex justify-content-center">
            <div className="spinner-border" role="status">
              <span className="sr-only">Loading...</span>
            </div>
          </div>}
        </CCard>
        </CCol>
       </CRow>
    </>
  )
}
  }

const mapStateToProps = state => ({
  ...state
});
export default connect(mapStateToProps, {
get_buisnsess_data,delete_buisness
})(Allbuisness);
